import React, { Component } from "react";

import { Link } from "react-router-dom";

export default class NavBar extends Component {

  state= {
    loggedIn: null,
    student: null,
    contact: null,
    admin: null
  }

  async componentDidMount(){
      const token = localStorage.getItem("token");
      if (token!=null) {
        this.setState({loggedIn: true});
        const login = await localStorage.getItem("login");
        //console.log(login); //for testing
        if (login==="Student"){
          this.setState({student: true});
        }
        else if (login==="Contact"){
          this.setState({contact: true});
        }
        else if (login==="Admin"){
          this.setState({admin: true});
        }
      }
      else{
        this.setState({loggedIn: false});
      }
      
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
          <div className ="col-12 text-center">
          <a className="navbar-brand" href='index.html'>
            Careers Hub Opportunity Finder
          </a>
          {!this.state.loggedIn ? (<div><Link className="navbar-brand hubLink" to="/user/login">Log in</Link>
          <Link className="navbar-brand hubLink" to="/user/register" style={{marginLeft: "20px"}}>Register</Link></div>) : 
          (<div><Link className="navbar-brand hubLink" style={{marginLeft: "20px"}} to="/user/logout">Log out</Link></div>)}
          {this.state.student ? (<span>
            <Link className="navbar-brand hubLink" to="/advertising/organisationList">View Organisations</Link>
            <Link className="navbar-brand hubLink" to="/advertising/opportunityList">View Opportunities</Link>
            <Link className="navbar-brand hubLink" to="/matching/mySkills">My Skills</Link>
          </span>) 
          : this.state.contact ? (<span>
            <Link className="navbar-brand hubLink" to="/advertising/opportunityList">View All Opportunities</Link>
            <Link className="navbar-brand hubLink" to="/advertising/myOpportunityList">View My Opportunities</Link>
            <Link className="navbar-brand hubLink" to="/advertising/addNewOpportunity">Add Opportunity</Link>
          </span>) 
          : this.state.admin ? (<span>
            <Link className="navbar-brand hubLink" to="/matching/addNewSkill">Add Skill</Link>
            <Link className="navbar-brand hubLink" to="/matching/skillList">View Skills</Link>
            {/*<Link className="navbar-brand hubLink" to="/matching/addNewQualification">Add Qualification</Link>
            <Link className="navbar-brand hubLink" to="/matching/qualificationList">View Qualifications</Link>*/}
            <Link className="navbar-brand hubLink" to="/advertising/addNewContact">Add Contact</Link>
            <Link className="navbar-brand hubLink" to="/advertising/contactList">View Contacts</Link>
            <Link className="navbar-brand hubLink" to="/advertising/addNewOrganisation">Add Organisation</Link>
            <Link className="navbar-brand hubLink" to="/advertising/organisationList">View Organisations</Link>
            <Link className="navbar-brand hubLink" to="/advertising/addNewOpportunity">Add Opportunity</Link>
            <Link className="navbar-brand hubLink" style={{marginLeft: "20px"}} to="/advertising/opportunityList">View Opportunities</Link>
          </span>) 
          : (null)}
          </div>
        </nav>
      </div>
    );
  }
}
