import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

class loginFailed extends Component {

    state={timeLeft: "5",
            redirect: null}

    async componentDidMount(){
        setTimeout(() => {this.setState({timeLeft: "4"});}, 1000);
        setTimeout(() => {this.setState({timeLeft: "3"});}, 2000);
        setTimeout(() => {this.setState({timeLeft: "2"});}, 3000);
        setTimeout(() => {this.setState({timeLeft: "1"});}, 4000);
        setTimeout(() => {this.setState({redirect: true});}, 5000);
    }

    render() {
        return (
            <div>
                {this.state.redirect ? (<Redirect to='/'></Redirect>) : (null)}
                <h1>Login failed</h1><br/>
                <h2>Returning to welcome page in <span style={{color:"red"}}>{this.state.timeLeft}</span></h2>
            </div>
        );
    }
}

export default loginFailed;