import React, { Component } from 'react';
import axios from "axios";

export default class ViewQualificationInfo extends Component {

    state = {
        course: null,
        level: ""
    };
    
    async componentDidMount(){
        const {id} = await this.props.match.params;
        const results = await axios.get(`https://careers-server.herokuapp.com/viewQualifications/?qualificationID=${id}`);
        await this.setState({course: results.data["qualificationList"][0].course,
                       level: results.data["qualificationList"][0].level});

    }
    

    render() {
        return (
            <React.Fragment>
               {this.state.course ? ( 
            <div>
                
                <h2>Qualification Details</h2><br/>
                <h4> {this.state.course}</h4>
                {this.state.level}<br/>               
                
            </div>
            ) : (<div>No qualification found</div>)}
            </React.Fragment>
        );
    }
}
