import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom'

export default class QualificationAdded extends Component {
    state = {
        qualification: null,
        problemQualification: null,
        errorInfo: null,
        urlchecker: "" //for testing purposes remove when done
    };


async componentDidMount() {
    const { qualification } = this.props.match.params;
    const url = `https://careers-server.herokuapp.com/addNewQualification?${qualification}`;
    //Attempts to get results set from API JSON
    try{
    const res = await axios.get(url);
    //console.log({qualification: res.data}); //for testing
    this.setState({qualification: res.data});
    }
    //if there is an error in API records the last part of the error message to return to the user
    catch (error){
      //console.log(error.response.data) //for testing
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({problemQualification: qualification})
      this.setState({errorInfo: seperateErrorMessages[3]})
    }
  }

  retryAddQualification(){
    return <Redirect to={`/matching/addNewQualification/${this.state.problemQualification}&${this.state.errorInfo}`} />
  }

    render() {
        return (
            <React.Fragment>
        {this.state.qualification ? (
          <div>
          
            {this.state.qualification.qualificationType.course}
            <br/><br/>
            {this.state.qualification.qualificationType.level}
            <br/><br/>
          </div>
        ) : ( this.state.errorInfo ? (
        <h1>{this.state.errorInfo} {this.retryAddQualification()}</h1>) :(<h1>Loading Contact</h1>)
        )}
      </React.Fragment>      
        );
    }
}


