import React, { Component } from 'react';
import axios from "axios";
import SkillCard from "./SkillCard";

export default class SkillList extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/viewSkills',
    skill: null,
    error: null
};

async componentDidMount() {
    const token = localStorage.getItem("token");
    if (token!=null){
        const login = localStorage.getItem("login");
        if (login==="Student"||login==="Admin"||login==="Contact")
        {
            await this.setState({url: `${this.state.url}?authenticationToken=${token}`})
        }
    }
    try{
    const results = await axios.get(this.state.url);
    //console.log(results.data["skillList"]); //for testing
    this.setState({skill: results.data["skillList"]});
    }
    catch(error){
        var seperateErrorMessages = error.response.data.split(":");
        this.setState({error: seperateErrorMessages[3]})
    }
}

    render() {
        return (
            <React.Fragment>
                {this.state.skill ? (
                
                <div className="row">
            {this.state.skill.map(skill => (
              <SkillCard
                key={skill.skillID.id}
                id={skill.skillID.id}
                name={skill.name}
                type={skill.category.skillType}
                sector={skill.category.businessSector}
                
              />
            ))} 
            </div>
                ) : this.state.error? (<div style={{color: "red"}}>{this.state.error}</div>) : (<h1>Loading Skills</h1>)}
          </React.Fragment>
        );
    }
}
