import React, { Component } from "react";
import axios from "axios";
import MatchCard from "../matching/matches/MatchCard";
import { findByLabelText } from "@testing-library/react";

export default class Dashboard extends Component {
  state = {
    matches: null,
    noMatches: null,
    studentID: "",
    studentLoggedIn: null,
    contactLoggedIn: null,
    adminLoggedIn: null,
  };

  async componentDidMount() {
    if (localStorage.getItem("refresh")) {
      await localStorage.removeItem("refresh");
      await window.location.reload(false);
    } else {
      const token = await localStorage.getItem("token");
      if (token != null) {
        const login = await localStorage.getItem("login");
        if (login === "Student") {
          //console.log("token = " + token); //for testing
          try {
            //use authentication token to check student's ID from back end
            const idCheck = await axios.get(
              `https://careers-server.herokuapp.com/retrieveStudentID/?authenticationToken=${token}`
            );
            const id = idCheck.data;
            //console.log("id = " + id); for testing
            await this.setState({ studentLoggedIn: true, studentID: id });

            //use students ID to fetch matches from back end
            const results = await axios.get(
              `https://careers-server.herokuapp.com/findMatches/?studentID=${id}&authenticationToken=${token}`
            );
            if (results.data["matchedOpportunities"][0] == null) {
              this.setState({ noMatches: "found" });
            } else {
              this.setState({ matches: results.data["matchedOpportunities"] });
            }
          } catch (error) {
            //console.log(error.response.data); //for testing
          }
        } else if (login === "Contact") {
          this.setState({ contactLoggedIn: true });
        } else if (login === "Admin") {
          this.setState({ adminLoggedIn: true });
        }
      }
    }
  }

  render() {
    return (
      <React.Fragment>
        {this.state.studentLoggedIn ? (
          <span>
            {this.state.noMatches ? (
              <div>No matches found</div>
            ) : this.state.matches ? (
              <div>
                <div>
                  <h1>
                    Opportunities matched for you, starting with closest matches
                  </h1>
                  <br />
                </div>
                <div className="row" style={{display: "flex", flexWrap: "wrap"}}>
                  {this.state.matches.map((match) => (
                    <MatchCard
                      key={match.opportunity.opportunityID.opportunityID}
                      opportunityID={
                        match.opportunity.opportunityID.opportunityID
                      }
                      opportunityTitle={match.opportunity.title}
                      organisationName={
                        match.opportunity.contact.organisation.name
                      }
                      organisationIsHidden={
                        match.opportunity.contact.organisation
                          .isHiddenFromStudents
                      }
                      opportunityCountry={match.opportunity.location.country}
                      opportunityRegion={match.opportunity.location.region}
                      opportunitySalary={match.opportunity.salary}
                      requiredSkillMatchRatio={match.requiredSkillsMatchRatio}
                      desiredSkillMatchRatio={match.desiredSkillsMatchRatio}
                      requiredSkillsGot={match.requiredSkillsMatched}
                      requiredSkillsNotChecked={match.requiredSkillsNotChecked}
                      desiredSkillsGot={match.desiredSkillsMatched}
                      desiredSkillsNotChecked={match.desiredSkillsNotChecked}
                      desiredSkillsNotGot={match.desiredSkillsLacked}
                      studentID={this.state.studentID}
                    />
                  ))}
                </div>
              </div>
            ) : (
              <h1>Loading Matches</h1>
            )}
          </span>
        ) : this.state.contactLoggedIn ? (
          <span>Contact dashboard goes here</span>
        ) : this.state.adminLoggedIn ? (
          <span>Admin dashboard goes here</span>
        ) : (<span><h2>Welcome to the Bournemouth University Careers Hub.</h2><br/><br/>
               <h4>In order to access the features of this website please log in</h4></span>)}
      </React.Fragment>
    );
  }
}
