import React, { Component } from 'react';
import axios from "axios";
import ContactCard from "./ContactCard";

export default class ContactList extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/viewContacts',
    contact: null,
    error: null
};

async componentDidMount() {
    const token = await localStorage.getItem("token");
    if (token!=null){
       await this.setState({url: `${this.state.url}/?authenticationToken=${token}`})
    }
    //console.log(this.state.url);
    try{
    const results = await axios.get(this.state.url);
    //console.log(results.data["contactList"]); //for testing
    this.setState({contact: results.data["contactList"]});
    }
    catch(error){
        //console.log(error.response.data); //for testing
        var seperateErrorMessages = error.response.data.split(":");
        this.setState({error: seperateErrorMessages[3]});
    }
}

    render() {
        return (
            <React.Fragment>
                {this.state.contact ? (
                
                <div className="row">
            {this.state.contact.map(contact => (
              <ContactCard
                key={contact.contactID.id}
                id={contact.contactID.id}
                firstName={contact.name.firstName}
                secondName={contact.name.secondName}
                telephone={contact.telephone}
                email={contact.email}
                
              />
            ))} 
            </div>
                ) : this.state.error ? (<div style={{color: "red"}}>{this.state.error}</div>) : (<h1>Loading Contacts</h1>)}
          </React.Fragment>
        );
    }
}
