import React, { Component } from 'react';
import axios from "axios";
import OpportunityCard from "./OpportunityCard";

export default class MyOpportunityList extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/viewOpportunities',
    opportunity: null
};

async componentDidMount() {
    const token = await localStorage.getItem("token");
    if (token!=null){
        const contactInfo = await axios.get(`https://careers-server.herokuapp.com/retrieveContactID/?authenticationToken=${token}`)
        //console.log(contactInfo.data); //for testing
        this.setState({url: `${this.state.url}/?authenticationToken=${token}&contactID=${contactInfo.data}`});
    }
    const results = await axios.get(this.state.url);
    //console.log(results.data["opportunityList"]); //for testing
    this.setState({opportunity: results.data["opportunityList"]});
}

    render() {
        return (
            <React.Fragment>
                {this.state.opportunity ? (
                
                <div className="row">
            {this.state.opportunity.map(opportunity => (
              <OpportunityCard
                key={opportunity.opportunityID.opportunityID}
                id={opportunity.opportunityID.opportunityID}
                title={opportunity.title}
                salary={opportunity.salary}
                country={opportunity.location.country}
                region={opportunity.location.region}
                closingDate={opportunity.closingDate}
                organisationName={opportunity.contact.organisation.name}
                organisationHiddenFromStudents={opportunity.contact.organisation.isHiddenFromStudents}
                
              />
            ))} 
            </div>
                ) : (<h1>Loading Opportunities</h1>)}
          </React.Fragment>
        );
    }
}
