import React, { Component } from "react";
import axios from "axios";

export default class ViewContactInfo extends Component {
  state = {
    contactID: "",
    firstName: null,
    secondName: "",
    telephone: "",
    email: "",
    verified: false,
    opportunities: null,
    authenticationToken: "",
    error: null,
  };

  async componentDidMount() {
    const { id } = await this.props.match.params;
    const token = await localStorage.getItem("token");
    if (token != null) {
      await this.setState({ authenticationToken: token });
    }
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/viewContacts/?contactID=${id}&authenticationToken=${this.state.authenticationToken}`
      );
      this.setState({
        firstName: results.data["contactList"][0].name.firstName,
        secondName: results.data["contactList"][0].name.secondName,
        telephone: results.data["contactList"][0].telephone,
        email: results.data["contactList"][0].email,
        verified: results.data["contactList"][0].isVerified,
        contactID: results.data["contactList"][0].contactID.id,
      });
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }

    //Another axios call will be needed here to get a list of opportunities at some point in the future
  }

  async unverify() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/undoVerifyContact/?id=${this.state.contactID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ verified: false });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  async verify() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/verifyContact/?id=${this.state.contactID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ verified: true });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  render() {
    return (
      <React.Fragment>
        {this.state.error ? (
          <div style={{ color: "red", wideth: "100%" }}>{this.state.error}</div>
        ) : null}
        {this.state.firstName ? (
          <div>
            <h2>Contact Details</h2>
            First Name: {this.state.firstName}
            <br />
            Second Name: {this.state.secondName}
            <br />
            Telephone: {this.state.telephone}
            <br />
            Email: {this.state.email}
            <br />
            <div className="toggleTablet">
              Verified?{" "}
              <React.Fragment>
                {this.state.verified ? (
                  <span style={{ color: "darkGreen", fontWeight: "bold" }}>
                    Yes
                    <br />
                    <button onClick={this.unverify.bind(this)}>Unverify</button>
                  </span>
                ) : (
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    No
                    <br />
                    <button onClick={this.verify.bind(this)}>Verify</button>
                  </span>
                )}
              </React.Fragment>
            </div>
            <br />
            <div className="divider"></div>
            <br />
            <h3>List of Created Opportunities</h3>
          </div>
        ) : (
          <div>No contact found</div>
        )}
      </React.Fragment>
    );
  }
}
