import React, { Component } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";

class register extends Component {
 
  state = {
    firstName: "",
    secondName: "",
    user: "",
    password: "",
    userCreated: null,
    error: null,
    
  };

  async handleFormSubmit(event) {
    event.preventDefault();
    const url = 'https://careers-server.herokuapp.com/addStudentUser';
    var results;
    try {
      await axios.post(url, {firstName: this.state.firstName, secondName: this.state.secondName, username: this.state.user, password: this.state.password}).then((response) => {
        results = response;
      }, (error) => {
        //console.log(error); //for testing
      });;
      //console.log(results); //for testing
      if (results.data.response === "User Created") {
        
        this.setState({userCreated: true})
      }else {
        this.setState({
          user: "",
          password: "",
          loginFailed: true,
        });
      }
    } catch (error) {
        var seperateErrorMessages = error.response.data.split(":");
        this.setState({error: seperateErrorMessages[3]})
    }
  }

  handleChange = (event) => {
    const input = event.target;
    const value = input.value;
    this.setState({ [input.name]: value });
  };

  render() {
    return (
        <div>
            {this.state.error ? (<span style={{color: "red"}}><h3>{this.state.error}</h3></span>) : (null)}
        <h2>Register as Student User</h2><br/>
      <form onSubmit={this.handleFormSubmit.bind(this)}>
        <label>
          First Name:{" "}
          <input
           style ={{marginRight: "5px"}}
            name="firstName"
            value={this.state.firstName}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <label style={{ marginLeft: "5px" }}>
          Second Name:{" "}
          <input
          style ={{marginRight: "32px"}}
            name="secondName"
            value={this.state.secondName}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <label>
          Username:{" "}
          <input
            name="user"
            value={this.state.user}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <label style={{ marginLeft: "5px" }}>
          Password:{" "}
          <input type="password"
            name="password"
            value={this.state.password}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <React.Fragment>
          {this.state.userCreated ? <Redirect to="/user/registrationSuccess" /> : null}
        </React.Fragment>
        <button type="submit">Register</button>
      </form>
      </div>
    );
  }
}

export default register;
