import React, { Component } from 'react';
import { Redirect } from "react-router-dom";

class RecordDeleted extends Component {

    state ={finished: null,
          url: '/'}

    async componentDidMount(){
        const reroute = await localStorage.getItem("redirect");
        await localStorage.removeItem("redirect");
        await this.setState({url: reroute});
        setTimeout(() => {this.setState({finished:true});}, 750);
    }
    render() {
        return (
            <div><div style={{textAlign:"center"}}>
                <h1>Record Deleted - Redirecting</h1></div>
                {this.state.finished ? (
                <Redirect to={this.state.url}></Redirect>)
                : (null) }
            </div>
        );
    }
}

export default RecordDeleted;