import React, { Component } from 'react';
import axios from "axios";
import MySkillCard from "./MySkillCard";

export default class MySkills extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/getStudentSkillList',
    studentID: "",
    skillsGot: null,
    skillsLacked: null,
    skillsNotChecked: null,
    error: null
};

async componentDidMount() {
    const token = localStorage.getItem("token");
    if (token!=null){
        const login = localStorage.getItem("login");
        if (login==="Student"||login==="Admin"||login==="Contact")
        {
            await this.setState({url: `${this.state.url}?authenticationToken=${token}`})
        }
    }
    try{
    const results = await axios.get(this.state.url);
    console.log(results.data); //for testing
    this.setState({studentID: results.data["studentID"]});
    if (results.data["studentHasTheseSkills"].length>0){
    this.setState({skillsGot: results.data["studentHasTheseSkills"]});
    }
    if (results.data["studentLacksTheseSkills"].length>0){
    this.setState({skillsLacked: results.data["studentLacksTheseSkills"]});
    }
    if (results.data["studentSkillsNotChecked"].length>0){
    this.setState({skillsNotChecked: results.data["studentSkillsNotChecked"]});
    }
    }
    catch(error){
        var seperateErrorMessages = error.response.data.split(":");
        this.setState({error: seperateErrorMessages[3]})
    }
}

    render() {
        return (
            <React.Fragment>
                {this.state.skillsGot ? (
                
                <div className="row" style={{border: "1px solid grey", borderRadius: "10px", marginBottom: "30px", backgroundColor: "silver"}}>
                    <div style={{width: "100%", marginBottom: "20px", backgroundColor: "lightGreen", borderRadius: "10px"}}><h2>Skills you have</h2></div>
            {this.state.skillsGot.map(skill => (
              <MySkillCard
                key={skill.skillID.id}
                skillID={skill.skillID.id}
                name={skill.name}
                type={skill.category.skillType}
                sector={skill.category.businessSector}
                skill="Got"
                studentID={this.state.studentID}
              />
            ))} 
            </div>
                ) : (null)}

            {this.state.skillsLacked ? (
                
                <div className="row" style={{border: "1px solid grey", borderRadius: "10px", marginBottom: "30px", backgroundColor: "silver"}}>
                    <div style={{width: "100%", marginBottom: "20px", backgroundColor: "pink", borderRadius: "10px"}}><h2>Skills you don't have</h2></div>
            {this.state.skillsLacked.map(skill => (
              <MySkillCard
                key={skill.skillID.id}
                skillID={skill.skillID.id}
                name={skill.name}
                type={skill.category.skillType}
                sector={skill.category.businessSector}
                skill="NotGot"
                studentID={this.state.studentID}
              />
            ))} 
            </div>
                ) : (null)}

            {this.state.skillsNotChecked ? (
                
                <div className="row" style={{border: "1px solid grey", borderRadius: "10px", marginBottom: "30px", backgroundColor: "silver"}}>
                    <div style={{width: "100%", marginBottom: "20px", backgroundColor: "lightYellow", borderRadius: "10px"}}><h2>Skills you haven't told us about</h2></div>
            {this.state.skillsNotChecked.map(skill => (
              <MySkillCard
                key={skill.skillID.id}
                skillID={skill.skillID.id}
                name={skill.name}
                type={skill.category.skillType}
                sector={skill.category.businessSector}
                skill="NotChecked"
                studentID={this.state.studentID}
              />
            ))} 
            </div>
                ) : (null)}
                
                 {this.state.error? (<div style={{color: "red"}}>{this.state.error}</div>) : (null)}

                 {this.state.skillsGot ? null : this.state.skillsLacked ? null : this.state.skillsNotChecked ? null : this.state.error ? null : (<h1>Loading Skills</h1>)}
          </React.Fragment>
        );
    }
}
