import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom'

export default class OrganisationAdded extends Component {
    state = {
        organisation: null,
        problemOrganisation: null,
        errorInfo: null,
        urlchecker: "" //for testing purposes remove when done
    };


async componentDidMount() {
    const { organisation } = this.props.match.params;
    const url = `https://careers-server.herokuapp.com/addNewOrganisation?${organisation}`;
    //Attempts to get results set from API JSON
    try{
    const res = await axios.get(url);
    //console.log({organisation: res.data}); //for testing
    this.setState({organisation: res.data});
    }
    //if there is an error in API records the last part of the error message to return to the user
    catch (error){
      //console.log(error.response.data) //for testing
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({problemOrganisation: organisation})
      this.setState({errorInfo: seperateErrorMessages[3]})
    }
  }

  retryAddOrganisation(){
    return <Redirect to={`/advertising/addNewOrganisation/${this.state.problemOrganisation}&${this.state.errorInfo}`} />
  }

    render() {
        return (
            <React.Fragment>
        {this.state.organisation ? (
          <div>
          
            {this.state.organisation.name}
            <br/><br/>
            {this.state.organisation.address.firstLine}
            <br/><br/>
            {this.state.organisation.address.secondLine.length>0 ? (this.state.organisation.address.secondLine + '<br/><br/>') : (null)}
            
            {this.state.organisation.address.thirdLine.length>0 ? (this.state.organisation.address.thirdLine + '<br/><br/>') : (null)}
            
            {this.state.organisation.address.town}
            <br/><br/>
            {this.state.organisation.address.county}
            <br/><br/>
            {this.state.organisation.address.postCode}
            <br/><br/>
            {this.state.organisation.address.country}
            <br/><br/>
            {this.state.organisation.description}
            <br/><br/>
            Verified? {this.state.organisation.isVerified ? ('Yes') : ('No')}
            <br/><br/>
            Hidden from students? {this.state.organisation.isHiddenFromStudents ? ('Yes') : ('No')}
          </div>
        ) : ( this.state.errorInfo ? (
        <h1>{this.state.errorInfo} {this.retryAddOrganisation()}</h1>) :(<h1>Loading Contact</h1>)
        )}
      </React.Fragment>      
        );
    }
}


