import React, { Component } from 'react';
import axios from "axios";
import OrganisationCard from "./OrganisationCard";

export default class OrganisationList extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/viewOrganisations',
    organisation: null
};

async componentDidMount() {
    const token = localStorage.getItem("token");
    if(token!=null){
        await this.setState({url: `${this.state.url}/?authenticationToken=${token}`})
    }
    const results = await axios.get(this.state.url);
    //console.log(results.data["organisationList"]); //for testing
    this.setState({organisation: results.data["organisationList"]});
}

    render() {
        return (
            <React.Fragment>
                {this.state.organisation ? (
                
                <div className="row">
            {this.state.organisation.map(organisation => (
              <OrganisationCard
                key={organisation.organisationID.organisationID}
                id={organisation.organisationID.organisationID}
                name={organisation.name}
                town={organisation.address.town}
                county={organisation.address.county}
                country={organisation.address.country}
                
              />
            ))} 
            </div>
                ) : (<h1>Loading Contacts</h1>)}
          </React.Fragment>
        );
    }
}
