import React, { Component } from "react";
import { Link } from "react-router-dom";

class AddNewOrganisation extends Component {
  state = {
    name: "",
    firstLine: "",
    secondLine: "",
    thirdLine: "",
    town: "",
    county: "",
    postCode: "",
    country: "",
    description: "",
    error: "",
    organisation: "",
    hiddenFromStudents: false,
    hiddenAsText: "false",
    authenticationToken: ""
  };

  async componentDidMount() {
    const token = localStorage.getItem("token");
    if (token!=null){
      await this.setState({authenticationToken: token});
    }
    const { organisation } = this.props.match.params;
    if (organisation) {
      var propertySplitter = organisation.split("&");
      this.setState({ name: propertySplitter[0].split("=")[1] });
      this.setState({ firstLine: propertySplitter[1].split("=")[1] });
      this.setState({ secondLine: propertySplitter[2].split("=")[1] });
      this.setState({ thirdLine: propertySplitter[3].split("=")[1] });
      this.setState({ town: propertySplitter[4].split("=")[1] });
      this.setState({ county: propertySplitter[5].split("=")[1] });
      this.setState({ postCode: propertySplitter[6].split("=")[1] });
      this.setState({ country: propertySplitter[7].split("=")[1] });
      this.setState({ description: propertySplitter[8].split("=")[1] });
      this.setState({ hiddenAsText: propertySplitter[9].split("=")[1] });
      await this.setState({ error: propertySplitter[11] });
      await this.setState({
        organisation: `name=${this.state.name}&firstLine=${this.state.firstLine}&secondLine=${this.state.secondLine}&thirdLine=${this.state.thirdLine}
          &town=${this.state.town}&county=${this.state.county}&postCode=${this.state.postCode}&country=${this.state.country}&description=${this.state.description}
          &hiddenAsText=${this.state.hiddenFromStudents}&authenticationToken=${this.state.authenticationToken}`
      });
     
    }
  }

  async updateName(e) {
    await this.setState({ name: e });
    this.updateContact();
  }

  async updateFirstLine(e) {
    await this.setState({ firstLine: e });
    this.updateContact();
  }

  async updateSecondLine(e) {
    await this.setState({ secondLine: e });
    this.updateContact();
  }

  async updateThirdLine(e) {
    await this.setState({ thirdLine: e });
    this.updateContact();
  }

  async updateTown(e) {
    await this.setState({ town: e });
    this.updateContact();
  }

  async updateCounty(e) {
    await this.setState({ county: e });
    this.updateContact();
  }

  async updatePostCode(e) {
    await this.setState({ postCode: e });
    this.updateContact();
  }

  async updateCountry(e) {
    await this.setState({ country: e });
    this.updateContact();
  }

  async updateHiddenFromStudents(e){
    if (e === true){
        await this.setState({hiddenFromStudents: true});
        await this.setState({hiddenAsText: "true"});
    }
    else {
      await this.setState({hiddenFromStudents: false})
      await this.setState({hiddenAsText: "false"});
    }
    //console.log(this.state.hiddenFromStudents); for testing
    this.updateContact();
  }

  async updateDescription(e) {
    await this.setState({ description: e });
    this.updateContact();
  }

  async updateContact() {
    await this.setState({
      organisation: `name=${this.state.name}&firstLine=${this.state.firstLine}&secondLine=${this.state.secondLine}&thirdLine=${this.state.thirdLine}
    &town=${this.state.town}&county=${this.state.county}&postCode=${this.state.postCode}&country=${this.state.country}&description=${this.state.description}
    &hiddenFromStudents=${this.state.hiddenAsText}&authenticationToken=${this.state.authenticationToken}`
    });
    //console.log(this.state.organisation); //for testing
  }

  render() {
    const { state } = this;
    return (
      <div className ="row">
        <form onSubmit={this.onSubmit}>
          <div className="formDivider">
            <label>
              Organisation Name:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.name}
                onChange={e => this.updateName(e.target.value)}
              />
            </label>
          
          <br />
          <br />
          
            <label>
              First Line of Address:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.firstLine}
                onChange={e => this.updateFirstLine(e.target.value)}
              />
            </label>
          <br />
          
            <label>
              Second Line of Address:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.secondLine}
                onChange={e => this.updateSecondLine(e.target.value)}
              />
            </label>
            <br />

            <label>
              Third Line of Address:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.thirdLine}
                onChange={e => this.updateThirdLine(e.target.value)}
              />
            </label>
            <br />

            <label>
              Town:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.town}
                onChange={e => this.updateTown(e.target.value)}
              />
            </label>
            <br />

            <label>
              County:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.county}
                onChange={e => this.updateCounty(e.target.value)}
              />
            </label>
            <br />
            
            <label>
              Post Code:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.postCode}
                onChange={e => this.updatePostCode(e.target.value)}
              />
            </label>
          
            <br />
            <br />
            <label>
              Country:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.country}
                onChange={e => this.updateCountry(e.target.value)}
              />
            </label>
            <br />
            <br />
            <label>
              Hide this organisation from Students:
              <input
                style={{ marginLeft: "20px" }}
                type="checkBox"
                value={state.hiddenFromStudents}
                onChange={e => this.updateHiddenFromStudents(e.target.checked)} />
              
            </label>
          </div>
            <br />
            <br />
            <div className="textAreaDivider">
            <label>
              <p style={{textAlign: 'center'}}>Organisation Description:</p>
              <textarea
                style={{ marginLeft: "20px", height: "200px", width: "500px" }}
                type="text"
                value={state.description}
                onChange={e => this.updateDescription(e.target.value)}
              />
            </label>
          </div>
          
          <br />

          <Link
            to={`/advertising/organisationAdded/${this.state.organisation}`}
          >
           <button>Add Organisation</button>
          </Link>
        </form>
        <br /> <br />
        <div style={{ color: "red", fontWeight: "bold" }}>
          {this.state.error}
        </div>
      </div>
    );
  }
}

export default AddNewOrganisation;
