import React, { Component } from "react";
import { Link } from "react-router-dom";

class AddNewSkill extends Component {
  state = {
    name: "",
    type: "",
    sector: "",
    skill: ""
  };

  async componentDidMount() {
    const { skill } = this.props.match.params;
    if (skill) {
      var propertySplitter = skill.split("&");
      this.setState({ name: propertySplitter[0].split("=")[1] });
      this.setState({ type: propertySplitter[1].split("=")[1] });
      this.setState({ sector: propertySplitter[2].split("=")[1] });
      await this.setState({ error: propertySplitter[3] });
      await this.setState({
        skill: `name=${this.state.name}&type=${this.state.type}&sector=${this.state.sector}`
      });
     
    }
  }

  async updateName(e) {
    await this.setState({ name: e });
    this.updateSkill();
  }

  async updateType(e) {
    await this.setState({ type: e });
    this.updateSkill();
  }

  async updateSector(e) {
    await this.setState({ sector: e });
    this.updateSkill();
  }

  async updateSkill() {
    await this.setState({
        skill: `name=${this.state.name}&type=${this.state.type}&sector=${this.state.sector}`
    });
    //console.log(this.state.skill); //for testing
  }

  render() {
    const { state } = this;
    return (
      <div className ="row">
        <form onSubmit={this.onSubmit}>
          <div className="formDivider">
            <label>
              Skill Name:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.name}
                onChange={e => this.updateName(e.target.value)}
              />
            </label>
          
          <br />
          <br />
          
            <label>
              Skill Type:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.type}
                onChange={e => this.updateType(e.target.value)}
              />
            </label>
          <br />
          <br />
          
            <label>
              Skill Sector:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.sector}
                onChange={e => this.updateSector(e.target.value)}
              />
            </label>
            <br />

          </div>
            
          <br />

          <Link
            to={`/matching/skillAdded/${this.state.skill}`}
          >
           <button>Add Skill</button>
          </Link>
        </form>
        <br /> <br />
        <div style={{ color: "red", fontWeight: "bold" }}>
          {this.state.error}
        </div>
      </div>
    );
  }
}

export default AddNewSkill;
