import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

class AddNewOpportunity extends Component {
  state = {
    opportunity: null,
    title: "",
    salary: "",
    country: "",
    region: "",
    contactID: "",
    details: "",
    howToApply: "",
    closingDate: "",
    closingDateTemp: "",
    contacts: null,
    error: "",
    adminLoggedIn: null,
    authenticationToken: "",
    selectClicked: null
  };

  async componentDidMount() {
    const { opportunity } = this.props.match.params;
    //check to see if user has logged in
    const token = await localStorage.getItem("token");
    if (token != null) {
      //if user has logged in add authentication token to local memory
      await this.setState({ authenticationToken: token });
      //check to see if user is logged in as an admin
      const login = await localStorage.getItem("login");
      if (login === "Admin") {
        //if user is logged in as admin change state
        await this.setState({ adminLoggedIn: true });
      }
    }
    if (opportunity) {
      var propertySplitter = opportunity.split("&");
      this.setState({ title: propertySplitter[0].split("=")[1] });
      this.setState({ salary: propertySplitter[1].split("=")[1] });
      this.setState({ country: propertySplitter[2].split("=")[1] });
      this.setState({ region: propertySplitter[3].split("=")[1] });
      this.setState({ contactID: propertySplitter[4].split("=")[1]});
      this.setState({ details: propertySplitter[5].split("=")[1] });
      this.setState({ howToApply: propertySplitter[6].split("=")[1] });
      this.setState({ closingDate: propertySplitter[7].split("=")[1] });
      /*console.log("title = " + this.state.title + ", salary = " + this.state.salary + ", country = " + this.state.country + ", region = " + this.state.region
                 + ", contactID = " + this.state.contactID + ", details = " + this.state.details + ", howToApply = " + this.state.howToApply +
                 ", closingDate = " + this.state.closingDate + ", authenticationToken = " + this.state.authenticationToken); //for testing */
      this.setState({
        opportunity: `title=${this.state.title}&salary=${this.state.salary}&country=${this.state.country}&region=${this.state.region}
                        &contactID=${this.state.contactID}&details=${this.state.details}
                        &howToApply=${this.state.howToApply}&closingDate=${this.state.closingDate}&authenticationToken=${this.state.authenticationToken}`
      });
      await this.setState({ error: propertySplitter[9] });
    }
    if (this.state.adminLoggedIn === true) {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/viewContacts?authenticationToken=${token}`
      );
      //console.log(results.data["contactList"]); //for testing
      this.setState({ contacts: results.data["contactList"]});
      /*console.log(this.state.contacts);
      console.log("contactID inside resultSet = " + this.state.contacts[0].contactID.id);
      console.log("contactID at component did mount = " + this.state.contactID);*/ //for testing
      
      //const options = results.data["organisationList"].map(organisations => {return key=organisations.organisationID.organisationID, value=organisation.name});
    }
  }

  async updateTitle(e) {
    await this.setState({ title: e });
    this.updateOpportunity();
  }

  async updateSalary(e) {
    await this.setState({ salary: e });
    this.updateOpportunity();
  }

  async updateCountry(e) {
    await this.setState({ country: e });
    this.updateOpportunity();
  }

  async updateRegion(e) {
    await this.setState({ region: e });
    this.updateOpportunity();
  }

  async updateDetails(e) {
    await this.setState({ details: e });
    this.updateOpportunity();
  }

  async updateHowToApply(e) {
    await this.setState({ howToApply: e });
    this.updateOpportunity();
  }

  async updateClosingDate(e) {
    var date = await new Date(e);
    await this.setState({ closingDate: date.getTime() });
    this.setState({ closingDateTemp: e });
    //console.log(this.state.closingDate); //for testing
    this.updateOpportunity();
  }

  async updateContactID(e) {
    await this.setState({ contactID: e });
    this.updateOpportunity();
  }
  async updateOpportunity() {
    this.setState({
      opportunity: `title=${this.state.title}&salary=${this.state.salary}&country=${this.state.country}&region=${this.state.region}&contactID=${this.state.contactID}&details=${this.state.details}
    &howToApply=${this.state.howToApply}&closingDate=${this.state.closingDate}&authenticationToken=${this.state.authenticationToken}`});
  }

  async addOptions(){
    if(this.state.selectClicked==null){
    await this.setState({contactID: this.state.contacts[0].contactID.id});
    this.setState({selectClicked: true});
    }
    //console.log("contactID after hover = " + this.state.contactID); for testing
  }

  render() {
    const { state } = this;
    return (
      <div className="row">
        
        <form onSubmit={this.onSubmit}>
          <div className="formDivider" style={{ width: "398px" }}>
            <label>
              Opportunity Title:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.title}
                onChange={(e) => this.updateTitle(e.target.value)}
              />
            </label>

            <label>
              Salary Information:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.salary}
                onChange={(e) => this.updateSalary(e.target.value)}
              />
            </label>

            <label>
              Details:
              <input
                style={{ marginLeft: "20px" }}
                type="textArea"
                value={state.details}
                onChange={(e) => this.updateDetails(e.target.value)}
              />
            </label>

            <br />
            <label>
              How to Apply:
              <input
                style={{ marginLeft: "20px" }}
                type="textArea"
                value={state.howToApply}
                onChange={(e) => this.updateHowToApply(e.target.value)}
              />
            </label>

            <br />
            <label>
              Closing Date:
              <input
                style={{ marginLeft: "20px" }}
                type="date"
                value={state.closingDateTemp}
                onChange={(e) => this.updateClosingDate(e.target.value)}
              />
            </label>
            <br />
            <br />
            <h6>Location of Opportunity</h6>
            <label>
              Country:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.country}
                onChange={(e) => this.updateCountry(e.target.value)}
              />
            </label>

            <br />
            <label>
              Region:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.region}
                onChange={(e) => this.updateRegion(e.target.value)}
              />
            </label>
            <br />
          </div>
          <br />
          {this.state.adminLoggedIn ? (
            <div>
              {this.state.contacts ? (
                <label>
                  Please select the contact from the list: <br />
                  <select
                    style={{ marginLeft: "20px", marginTop: "10px", minWidth:"400px"}}
                    value={state.contactID}
                    onMouseEnter={this.addOptions.bind(this)}
                    onChange={(e) => this.updateContactID(e.target.value)}
                  >
                    
                    {this.state.selectClicked ? (<React.Fragment>
                    {this.state.contacts.map((contacts) => {
                      return (
                        <option
                          key={contacts.contactID.id}
                          value={contacts.contactID.id}
                        >
                          {contacts.name.secondName}, {contacts.name.firstName}.{" "}
                          {contacts.organisation.name}
                        </option>
                      );
                    })} </React.Fragment>) : (<option>Please Select An Option</option>)}
                    
                  </select>
                </label>
              ) : (
                <div>No contacts have been added to the system. A contact is required to add an opportunity</div>
              )}
               <br/>
               
            </div>
           
          ) : null}

          {this.state.opportunity ? (
          <Link to={`/advertising/opportunityAdded/${this.state.opportunity}`}>
            <button style={{marginTop: "10px"}}>
            Add Opportunity
            </button>
          </Link>) : (null)}
        </form>
        <br /> <br />
        <div style={{ color: "red", fontWeight: "bold", width: "100%" }}>
          {this.state.error}
        </div>
      </div>
    );
  }
}

export default AddNewOpportunity;
