import React, { Component } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";
import dontHaveButtonSprite from '../../resources/dontHaveButtonSprite.png';
import haveButtonSprite from '../../resources/haveButtonSprite.png';
import removeButtonSprite from '../../resources/removeButtonSprite.png';

class SkillsItem extends Component {
  state = {
    name: "",
    skillID: "",
    studentID: "",
    skillStatus: "",
    refresh: null,
    skillGotUrl: "",
    skillLackedUrl: "",
    removeSkillUrl: "",
    token: "not working"
  };

  async componentDidMount() {
    const { name, skillID, studentID, skillStatus} = this.props;
    this.setState({
      name,
      skillID,
      studentID,
      skillStatus
    });
    
    const tempToken = await localStorage.getItem("token");
    //console.log("tempToken = "+tempToken); //for testing
    if (tempToken!=null){
      this.setState({token: tempToken});
    }
    //console.log("Token = " +this.state.token); //for testing

    this.setState({
      skillGotUrl: `https://careers-server.herokuapp.com/addSkillGotToStudent/?studentID=${studentID}&skillID=${skillID}&authenticationToken=${this.state.token}`,
      skillLackedUrl: `https://careers-server.herokuapp.com/addSkillNotGotToStudent/?studentID=${this.state.studentID}&skillID=${this.state.skillID}&authenticationToken=${this.state.token}`,
      removeSkillUrl: `https://careers-server.herokuapp.com/removeSkillChoiceFromStudent/?studentID=${this.state.studentID}&skillID=${this.state.skillID}&authenticationToken=${this.state.token}`
    });

  }

  async addSkillGot() {
    const results = await axios.get(this.state.skillGotUrl);
    //console.log(results.data); //for testing
    if (results.data === "Done") {
      this.setState({ refresh: true });
    }
  }

  async addSkillLacked() {
    const results = await axios.get(this.state.skillLackedUrl);
    //console.log(results.data); //for testing

    if (results.data === "Done") {
      this.setState({ refresh: true });
    }
  }

  async removeSkill() {
    const results = await axios.get(this.state.removeSkillUrl);
    //console.log(results.data); //for testing

    if (results.data === "Done") {
      this.setState({ refresh: true });
    }
  }

  render() {
    return (
      <div style={{borderRadius: '5px', borderTopLeftRadius: '0px', borderTopRightRadius: '0px', borderTop: '1px solid grey', backgroundColor: 'white'}}>
        <React.Fragment>
          {this.state.refresh ? <Redirect to="/refresh" /> : null}
        </React.Fragment>
        <div className='card-header card-title' style={{backgroundColor: 'rgba(0,0,0,.08)'}} >{this.state.name}
        {this.state.afterName}</div>
        <React.Fragment>
          {this.state.skillStatus === "Got" ? (
            <div className='card-body' style={{paddingTop:'0px', paddingBottom: '10px', paddingLeft: '0px', paddingRight: '0px',
                                               borderTop: '1px solid grey', borderBottom: '1px solid grey'}}>
              <button className="skillUpdate"
               
                onClick={this.addSkillLacked.bind(this)}
              >
                I don't have this skill <img className='imgBoxStyle' src={dontHaveButtonSprite} alt="don't have"></img>
              </button>
              <br />
              <button className="skillUpdate"
              style={{paddingLeft: '20px'}}
              onClick={this.removeSkill.bind(this)}>
                I'm unsure <img className='imgBoxStyle' src={removeButtonSprite} alt="don't have"></img>
              </button>
            </div>
          ) : null}
        </React.Fragment>
        <React.Fragment>
          {this.state.skillStatus === "NotGot" ? (
            <div className='card-body' style={{paddingTop:'0px', paddingBottom: '10px', paddingLeft: '0px', paddingRight: '0px',
                            borderTop: '1px solid grey'}}> 
              <button className="skillUpdate"
                style={{paddingLeft: '20px'}}
                onClick={this.addSkillGot.bind(this)}
              >
                I have this skill <img className='imgBoxStyle' src={haveButtonSprite} alt="don't have"></img>
              </button>
              <br />
              <button className="skillUpdate"
                style={{paddingLeft: '20px'}}
                onClick={this.removeSkill.bind(this)}>
                I'm unsure <img className='imgBoxStyle' src={removeButtonSprite} alt="don't have"></img>
              </button>
            </div>
          ) : null}
        </React.Fragment>
        <React.Fragment>
          {this.state.skillStatus === "NotChecked" ? (
            <div className='card-body' style={{paddingTop:'0px', paddingBottom: '10px', paddingLeft: '0px', paddingRight: '0px',
                                     borderTop: '1px solid grey'}}>
              <button className="skillUpdate"
               style={{paddingLeft: '20px'}}
                onClick={this.addSkillGot.bind(this)}
              >
                I have this skill <img className='imgBoxStyle' src={haveButtonSprite} alt="don't have"></img>
              </button>
              <br />
              <button className="skillUpdate"
                
                onClick={this.addSkillLacked.bind(this)}
              >
                I don't have this skill <img className='imgBoxStyle' src={dontHaveButtonSprite} alt="don't have"></img>
              </button>
            </div>
          ) : null}
        </React.Fragment>
      </div>
    );
  }
}

export default SkillsItem;
