import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom'

export default class OpportunityAdded extends Component {
    state = {
        opportunity: null,
        problemOpportunity: null,
        errorInfo: null
    };


async componentDidMount() {
    const { opportunity } = this.props.match.params;
    const url = `https://careers-server.herokuapp.com/addNewOpportunity?${opportunity}`;
    //Attempts to get results set from API JSON
    try{
    const res = await axios.get(url);
    //console.log({opportunity: res.data}); //for testing
    this.setState({opportunity: res.data});
    }
    //if there is an error in API records the last part of the error message to return to the user
    catch (error){
      //console.log(error.response.data) //for testing
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({problemOpportunity: opportunity})
      this.setState({errorInfo: seperateErrorMessages[3]})
    }
  }

  //If any errors are returned then go back to the add opportunity page
  retryAddOpportunity(){
    return <Redirect to={`/advertising/addNewOpportunity/${this.state.problemOpportunity}&${this.state.errorInfo}`} />
  }

    render() {
        return (
            <React.Fragment>
        {this.state.opportunity ? (
          <div>
          
            {this.state.opportunity.title}
            <br/><br/>
            {this.state.opportunity.salary}
            <br/><br/>
            {this.state.opportunity.location.country}
            <br/><br/>
            {this.state.opportunity.location.region}
            <br/><br/>
            {this.state.opportunity.details}
            <br/><br/>
            {this.state.opportunity.howToApply}
            <br/><br/>
            Created by:<br/>{this.state.opportunity.contact.name.secondName}, {this.state.opportunity.contact.name.firstName} of {this.state.opportunity.contact.organisation.name}
            <br/><br/>
            Date Created:<br/>{this.state.opportunity.dateCreated.split("T")[0].split("-")[2]}
            -{this.state.opportunity.dateCreated.split("T")[0].split("-")[1]}
            -{this.state.opportunity.dateCreated.split("T")[0].split("-")[0]}
            <br/><br/>
            Closing Date:<br/>{this.state.opportunity.closingDate.split("T")[0].split("-")[2]}
            /{this.state.opportunity.closingDate.split("T")[0].split("-")[1]}
            /{this.state.opportunity.closingDate.split("T")[0].split("-")[0]}
          </div>
        ) : ( this.state.errorInfo ? (
        <h1>{this.state.errorInfo} {this.retryAddOpportunity()}</h1>) :(<h1>Loading Opportunity</h1>)
        )}
      </React.Fragment>      
        );
    }
}


