import React, { Component } from "react";
import axios from "axios";
import ContactCard from "../contact/ContactCard";
import OpportunityCard from "../opportunity/OpportunityCard";
import { Redirect } from "react-router-dom";

export default class ViewContactInfo extends Component {
  state = {
    name: null,
    firstLine: "",
    secondLine: null,
    thirdLine: null,
    town: "",
    county: "",
    postCode: "",
    country: "",
    description: null,
    organisationID: null,
    verified: false,
    hiddenFromStudents: false,
    contacts: null,
    authenticationToken: "",
    error: null,
    studentLogin : null,
    opportunities: null,
    opportunityError: null
  };

  async componentDidMount() {
    const login = await localStorage.getItem("login");
    if (login === "Student"){
      this.setState({studentLogin: true})
    }
    const { id } = await this.props.match.params;
    var url = `https://careers-server.herokuapp.com/viewOrganisations/?organisationID=${id}`;
    const token = await localStorage.getItem("token");
    if (token != null) {
      url = `${url}&authenticationToken=${token}`;
      await this.setState({ authenticationToken: token });
    }
    try {
      const results = await axios.get(url);
      await this.setState({
        name: results.data["organisationList"][0].name,
        firstLine: results.data["organisationList"][0].address.firstLine,
        town: results.data["organisationList"][0].address.town,
        county: results.data["organisationList"][0].address.county,
        postCode: results.data["organisationList"][0].address.postCode,
        country: results.data["organisationList"][0].address.country,
        description: results.data["organisationList"][0].description,
        organisationID:
          results.data["organisationList"][0].organisationID.organisationID,
        verified: results.data["organisationList"][0].isVerified,
        hiddenFromStudents:
          results.data["organisationList"][0].isHiddenFromStudents,
      });
      //console.log(this.state.organisationID); //for testing
      if (results.data["organisationList"][0].address.secondLine !== "") {
        this.setState({
          secondLine: results.data["organisationList"][0].address.secondLine,
        });
      }
      if (results.data["organisationList"][0].address.thirdLine !== "") {
        this.setState({
          thirdLine: results.data["organisationList"][0].address.thirdLine,
        });
      }
      if (this.state.organisationID) {
        if (login !== "Student"){
        var contactUrl = `https://careers-server.herokuapp.com/viewContacts/?organisationID=${this.state.organisationID}`;
        if (token != null) {
          contactUrl = `${contactUrl}&authenticationToken=${token}`;
        }
        const contactResults = await axios.get(contactUrl);
        this.setState({ contacts: contactResults.data["contactList"] });
        //console.log(this.state.contacts); //for testing
      }
      else {
        var opportunityUrl = `https://careers-server.herokuapp.com/findOpportunitiesByOrganisation/?organisationID=${this.state.organisationID}`;
        if (token != null) {
          opportunityUrl = `${opportunityUrl}&authenticationToken=${token}`;
        }
        try{
        const opportunityResults = await axios.get(opportunityUrl);
          this.setState({opportunities: opportunityResults.data["opportunityList"]});
        }
        catch(error){
          var seperateOpportunityErrorMessages = error.response.data.split(":");
         this.setState({ opportunityError: seperateOpportunityErrorMessages[3] });
        }
        
      }
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }

    //Another axios call will be needed here to get a list of opportunities at some point in the future
  }

  async unverify() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/undoVerifyOrganisation/?id=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ verified: false });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  async verify() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/verifyOrganisation/?id=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`
      );
      // console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ verified: true });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  async reveal() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/revealOrganisation/?id=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ hiddenFromStudents: false });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  async hide() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/hideOrganisation/?id=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        this.setState({ hiddenFromStudents: true });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  async delete() {
    try {
      const results = await axios.get(
        `https://careers-server.herokuapp.com/removeOrganisation/?id=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`
      );
      //console.log(results.data); //for testing

      if (results.data === "Done") {
        await localStorage.setItem(
          "redirect",
          "/advertising/organisationList/"
        );
        this.setState({ organisationDeleted: true });
      }
    } catch (error) {
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({ error: seperateErrorMessages[3] });
    }
  }

  render() {
    return (
      <React.Fragment>
        {this.state.error ? (<div style={{color:"red", fontWeight: "bold"}}>{this.state.error}</div>) : (null)}
        {this.state.organisationDeleted ? (
          <Redirect to="/recordDeleted"></Redirect>
        ) : null}
        {this.state.name ? (
          <div>
            <h2>Organisation Details</h2>
            <br />
            <h4>{this.state.name}</h4>
            {this.state.firstLine}
            <br />
            {this.state.secondLine ? (
              <React.Fragment>
                {this.state.secondLine}
                <br />
              </React.Fragment>
            ) : null}
            {this.state.thirdLine ? (
              <React.Fragment>
                {this.state.thirdLine}
                <br />
              </React.Fragment>
            ) : null}
            {this.state.town}
            <br />
            {this.state.county}
            <br />
            {this.state.postCode}
            <br />
            {this.state.country}
            <br />
            {this.state.studentLogin ? (null) : (<React.Fragment>
            <div className="toggleTablet" style={{ marginTop: "10px" }}>
              Verified?{" "}
              <React.Fragment>
                {this.state.verified ? (
                  <span style={{ color: "darkGreen", fontWeight: "bold" }}>
                    Yes <br />
                    <button
                      className="generalButton"
                      onClick={this.unverify.bind(this)}
                    >
                      Unverify
                    </button>
                  </span>
                ) : (
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    No <br />
                    <button
                      className="generalButton"
                      onClick={this.verify.bind(this)}
                    >
                      Verify
                    </button>
                  </span>
                )}
              </React.Fragment>
            </div>
           
            <div className="toggleTablet" style={{ width: "240px" }}>
              Hidden from students?{" "}
              <React.Fragment>
                {this.state.hiddenFromStudents ? (
                  <span style={{ color: "darkGreen", fontWeight: "bold" }}>
                    Yes <br />
                    <button
                      className="generalButton"
                      onClick={this.reveal.bind(this)}
                    >
                      Reveal
                    </button>
                  </span>
                ) : (
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    No <br />
                    <button
                      className="generalButton"
                      onClick={this.hide.bind(this)}
                    >
                      Hide
                    </button>
                  </span>
                )}
              </React.Fragment>
            </div>
            <br />
            
            <div>
              <button className="deleteButton" onClick={this.delete.bind(this)}>
                Delete Organisation
              </button>
            </div>
            </React.Fragment>)}
            <br />
            

            <h5>Organisation Description:</h5>
            {this.state.description}
            <br />
            <br />
            <div className="divider"></div>
            <br />
            {this.state.studentLogin ? (<React.Fragment><h3>List of Current Opportunities for {this.state.name}</h3><br></br>
            {this.state.opportunities ? (
            <div className="row">
            {this.state.opportunities.map(opportunity => (
              <OpportunityCard
                key={opportunity.opportunityID.opportunityID}
                id={opportunity.opportunityID.opportunityID}
                title={opportunity.title}
                salary={opportunity.salary}
                country={opportunity.location.country}
                region={opportunity.location.region}
                closingDate={opportunity.closingDate}
                organisationName={opportunity.contact.organisation.name}
                organisationHiddenFromStudents={opportunity.contact.organisation.isHiddenFromStudents}
                
              />
            ))} 
            </div>) : this.state.opportunityError ? (<div style={{fontWeight:"bold", color: "red"}}>{this.state.opportunityError}</div>) : (null)}</React.Fragment>) : (
            <h3>List of Contacts for {this.state.name}</h3>)}
            <br />
            {this.state.studentLogin ? (null) : this.state.contacts ? (
              ((
                <div className="row">
                  {this.state.contacts.map((contact) => (
                    <ContactCard
                      key={contact.contactID.id}
                      id={contact.contactID.id}
                      firstName={contact.name.firstName}
                      secondName={contact.name.secondName}
                      telephone={contact.telephone}
                      email={contact.email}
                    />
                  ))}
                </div>
              ),
              this.state.contacts[0] ? null : (
                <div>
                  No contacts are currently associated with {this.state.name}
                </div>
              ))
            ) : (
              <h1>Loading Contacts</h1>
            )}
          </div>
        ) : (
          <div>No organisation found</div>
        )}
      </React.Fragment>
    );
  }
}
