import React, { Component } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";

class login extends Component {
 
  state = {
    user: "",
    password: "",
    loginSuccess: null,
    loginFailed: null,
    errorMessage: null
    
  };

  async handleFormSubmit(event) {
    event.preventDefault();
    const url = 'https://careers-server.herokuapp.com/checkLogin';
    var results;
    try {
      await axios.post(url, {username: this.state.user, password: this.state.password}).then((response) => {
        results = response;
      }, (error) => {
        this.setState({errorMessage: error.response.data.split(":")[3]});
      });;
      //console.log(results); //for testing
      if (results.data.response === "Student User Found") {
        await localStorage.setItem("token", results.data.token);
        await localStorage.setItem("login", "Student");
        await localStorage.setItem("refresh", true); //lets the dashboard know it needs to refresh (this causes Navbar to update login/register/logout buttons)
        this.setState({
          user: "",
          password: "",
          loginSuccess: true,
        });
      } else if (results.data.response === "Contact User Found") {
        await localStorage.setItem("token", results.data.token);
        await localStorage.setItem("login", "Contact");
        await localStorage.setItem("refresh", true); //lets the dashboard know it needs to refresh (this causes Navbar to update login/register/logout buttons)
        this.setState({
          user: "",
          password: "",
          loginSuccess: true,
        });
      } else if (results.data.response === "Admin User Found") {
        await localStorage.setItem("token", results.data.token);
        await localStorage.setItem("login", "Admin");
        await localStorage.setItem("refresh", true); //lets the dashboard know it needs to refresh (this causes Navbar to update login/register/logout buttons)
        this.setState({
          user: "",
          password: "",
          loginSuccess: true,
        });
      }else {
        this.setState({
          user: "",
          password: "",
          loginFailed: true,
        });
      }
    } catch (error) {
      //console.log(error); //for testing
    }
  }

  handleChange = (event) => {
    const input = event.target;
    const value = input.value;
    this.setState({ [input.name]: value });
  };

  render() {
    return (
      <div>
      {this.state.errorMessage ? (<div style={{color: "red", fontWeight: "bold"}}>{this.state.errorMessage}</div>) : (null)}
      <form onSubmit={this.handleFormSubmit.bind(this)}>
        <label>
          Username:{" "}
          <input
            name="user"
            value={this.state.user}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <label style={{ marginLeft: "5px" }}>
          Password:{" "}
          <input
            type="password"
            name="password"
            value={this.state.password}
            onChange={this.handleChange}
          />
        </label>
        <br />
        <React.Fragment>
          {this.state.loginSuccess ? <Redirect to="/" /> : null}
        </React.Fragment>
        <React.Fragment>
          {this.state.loginFailed ? <Redirect to="/user/loginFailed"/> : null}
        </React.Fragment>
        <button type="submit">Sign In</button>
      </form>
      </div>
    );
  }
}

export default login;
