import React, { Component } from 'react';
import axios from "axios";
import QualificationCard from "./QualificationCard";

export default class QualificationList extends Component {

state = {
    url: 'https://careers-server.herokuapp.com/viewQualifications',
    qualification: null
};

async componentDidMount() {
    const results = await axios.get(this.state.url);
    //console.log(results.data["qualificationList"]); //for testing
    this.setState({qualification: results.data["qualificationList"]});
}

    render() {
        return (
            <React.Fragment>
                {this.state.qualification ? (
                
                <div className="row">
            {this.state.qualification.map(qualification => (
              <QualificationCard
                key={qualification.qualificationID.id}
                id={qualification.qualificationID.id}
                course={qualification.qualificationType.course}
                level={qualification.qualificationType.level}
              />
            ))} 
            </div>
                ) : (<h1>Loading Contacts</h1>)}
          </React.Fragment>
        );
    }
}
