import React, { Component } from 'react';
import { Link } from "react-router-dom";
import axios from "axios";


class AddNewContact extends Component {

    state = {
        firstName: "",
        secondName: "",
        telephone: "",
        email: "",
        error: "",
        contact: "",
        organisationID: "",
        organisations: null,
        options: null,
        authenticationToken: ""
    };

    async componentDidMount(){
          var url = "https://careers-server.herokuapp.com/viewOrganisations"
          const token = localStorage.getItem("token");
          if (token!=null){
              url = `${url}/?authenticationToken=${token}`;
              await this.setState({authenticationToken: token});
          }
          const {contact} = this.props.match.params;
          if (contact){
          var propertySplitter = contact.split("&");
          this.setState({firstName : propertySplitter[0].split("=")[1]})
          this.setState({secondName : propertySplitter[1].split("=")[1]})
          this.setState({telephone : propertySplitter[2].split("=")[1]})
          this.setState({email : propertySplitter[3].split("=")[1]})
          this.setState({organisationID: propertySplitter[4].split("=")[1]})
          await this.setState({error : propertySplitter[6]})
          this.setState({contact : `firstName=${this.state.firstName}&secondName=${this.state.secondName}&telephone=${this.state.telephone}&email=${this.state.email}&organisationID=${this.state.organisationID}`});                 
        }
        const results = await axios.get(url);   
        //console.log(results.data["organisationList"]); //for testing
          this.setState({organisations: results.data["organisationList"], options: results.data["organisationList"],
                    organisationID: results.data["organisationList"][0].organisationID.organisationID});
          //const options = results.data["organisationList"].map(organisations => {return key=organisations.organisationID.organisationID, value=organisation.name});
                    
    }

async updateFirstName(e){
    await this.setState({firstName: e});
    this.updateContact();
}

async updateSecondName(e){
    await this.setState({secondName: e});
    this.updateContact();
}

async updateTelephone(e){
    await this.setState({telephone: e});
    this.updateContact();
}

async updateEmail(e){
    await this.setState({email: e});
    this.updateContact();
}

async updateOrganisationID(e){
    await this.setState({organisationID: e});
    this.updateContact();
}
async updateContact(){
    this.setState({contact : `firstName=${this.state.firstName}&secondName=${this.state.secondName}&telephone=${this.state.telephone}
    &email=${this.state.email}&organisationID=${this.state.organisationID}&authenticationToken=${this.state.authenticationToken}`});
}
                        

    render() {
        const{state}=this;
        return (
                <div className="row" >
                <form onSubmit = {this.onSubmit}>
                    <div className="formDivider" style={{width: '398px'}}>
                    <label>
                        Contact First Name: 
                        <input style={{marginLeft: "20px"}} type="text" value={state.firstName} onChange={ e => this.updateFirstName(e.target.value) }/>
                    </label>
                    
                    <br /><br />
                    <label>
                        Contact Second Name: 
                        <input style={{marginLeft: "20px"}} type="text" value={state.secondName} onChange={ e => this.updateSecondName(e.target.value) }/>
                    </label>
                    
                    <br /><br />
                    <label>
                        Contact Phone Number: 
                        <input style={{marginLeft: "20px"}} type="text" value={state.telephone} onChange={ e => this.updateTelephone(e.target.value) }/>
                    </label>
                    
                    <br /><br />
                    <label>
                        Contact Email Address: 
                        <input style={{marginLeft: "20px"}} type="text" value={state.email} onChange={ e => this.updateEmail(e.target.value) }/>
                    </label>     
                    </div>    
                    <br />
                    {this.state.organisations ? (<label >
                    Please select your Organisation from the list: <br />
                     <select style={{marginLeft: "20px", marginTop: "10px"}} value={state.organisationID} onChange={ e => this.updateOrganisationID(e.target.value) }>
                     {this.state.organisations.map(organisations => {
                        return <option key={organisations.organisationID.organisationID} value={organisations.organisationID.organisationID}>{organisations.name}</option>;
              })}</select>

        </label>) : (<div>Loading Organisations</div>)}
        

                    <br /><br />
                    
                    <Link to={`/advertising/contactAdded/${this.state.contact}`}>Add Contact</Link>
                    
                </form>
                <br/> <br/>
                <div style={{color:'red', fontWeight:'bold'}} >{this.state.error}</div>
                </div>
        );
    }
}

export default AddNewContact;