import React from 'react';
import { HashRouter as Router, Route, Switch } from "react-router-dom";

import "bootstrap/dist/css/bootstrap.min.css";

import './App.css';
import RedirectToDashboard from './RedirectToDashboard';
import RecordDeleted from './RecordDeleted';
import NavBar from "./components/layout/NavBar";
import Dashboard from "./components/layout/Dashboard";

import Login from './components/user/login';
import Register from './components/user/register';
import Logout from './components/user/logout';
import LoginFailed from './components/user/loginFailed';

import AddNewContact from "./components/advertising/contact/AddNewContact";
import ContactAdded from "./components/advertising/contact/ContactAdded";
import ContactList from "./components/advertising/contact/ContactList";
import ViewContactInfo from "./components/advertising/contact/ViewContactInfo";

import AddNewOrganisation from "./components/advertising/organisation/AddNewOrganisation";
import OrganisationAdded from "./components/advertising/organisation/OrganisationAdded";
import OrganisationList from "./components/advertising/organisation/OrganisationList";
import ViewOrganisationInfo from "./components/advertising/organisation/ViewOrganisationInfo";

import AddNewOpportunity from "./components/advertising/opportunity/AddNewOpportunity";
import OpportunityAdded from "./components/advertising/opportunity/OpportunityAdded";
import OpportunityList from "./components/advertising/opportunity/OpportunityList";
import MyOpportunityList from "./components/advertising/opportunity/MyOpportunityList";
import ViewOpportunityInfo from "./components/advertising/opportunity/ViewOpportunityInfo";

import AddNewSkill from "./components/matching/skill/AddNewSkill";
import SkillAdded from "./components/matching/skill/SkillAdded";
import SkillList from "./components/matching/skill/SkillList";
import MySkills from "./components/matching/skill/MySkills";
import MySkillChanged from "./components/matching/skill/MySkillChanged";
import ViewSkillInfo from "./components/matching/skill/ViewSkillInfo";

import AddNewQualification from "./components/matching/qualification/AddNewQualification";
import QualificationAdded from "./components/matching/qualification/QualificationAdded";
import QualificationList from "./components/matching/qualification/QualificationList";
import ViewQualificationInfo from "./components/matching/qualification/ViewQualificationInfo";
import RegistrationSuccess from './components/user/registrationSuccess';

function App() {
  return (
    <Router>
      <div className="App">
        <NavBar />
        <div className="MainContainer">
        <Switch>
          <Route exact path ="/" component={Dashboard} />
          <Route exact path ="/refresh" component={RedirectToDashboard} />
          <Route exact path ="/recordDeleted" component={RecordDeleted} />
          <Route exact path ="/user/login" component={Login} />
          <Route exact path ="/user/register" component={Register} />
          <Route exact path="/user/registrationSuccess" component={RegistrationSuccess} />
          <Route exact path ='/user/logout' component={Logout} />
          <Route exact path ='/user/loginFailed' component={LoginFailed} />
          <Route exact path ="/advertising/addNewContact/" component={AddNewContact} />
          <Route exact path ="/advertising/contactAdded/:contact" component={ContactAdded} />
          <Route exact path ="/advertising/addNewContact/:contact" component={AddNewContact} />
          <Route exact path ="/advertising/contactList/" component={ContactList} />
          <Route exact path ="/advertising/viewContactInfo/:id" component={ViewContactInfo} />
          <Route exact path ="/advertising/addNewOrganisation/" component={AddNewOrganisation} />
          <Route exact path ="/advertising/organisationAdded/:organisation" component={OrganisationAdded} />
          <Route exact path ="/advertising/addNewOrganisation/:organisation" component={AddNewOrganisation} />
          <Route exact path ="/advertising/organisationList/" component={OrganisationList} />
          <Route exact path ="/advertising/viewOrganisationInfo/:id" component={ViewOrganisationInfo} />
          <Route exact path ="/advertising/addNewOpportunity/" component={AddNewOpportunity} />
          <Route exact path ="/advertising/opportunityAdded/:opportunity" component={OpportunityAdded} />
          <Route exact path ="/advertising/addNewOpportunity/:opportunity" component={AddNewOpportunity} />
          <Route exact path ="/advertising/opportunityList/" component={OpportunityList} />
          <Route exact path ="/advertising/myOpportunityList/" component={MyOpportunityList} />
          <Route exact path ="/advertising/viewOpportunityInfo/:id" component={ViewOpportunityInfo} />
          <Route exact path ="/matching/addNewSkill/" component={AddNewSkill} />
          <Route exact path ="/matching/skillAdded/:skill" component={SkillAdded} />
          <Route exact path ="/matching/addNewSkill/:skill" component={AddNewSkill} />
          <Route exact path ="/matching/skillList/" component={SkillList} />
          <Route exact path ="/matching/mySkills/" component={MySkills} />
          <Route exact path ="/mySkillChanged/" component={MySkillChanged} />
          <Route exact path ="/matching/viewSkillInfo/:id" component={ViewSkillInfo} />
          <Route exact path ="/matching/addNewQualification/" component={AddNewQualification} />
          <Route exact path ="/matching/qualificationAdded/:qualification" component={QualificationAdded} />
          <Route exact path ="/matching/addNewQualification/:qualification" component={AddNewQualification} />
          <Route exact path ="/matching/qualificationList/" component={QualificationList} />
          <Route exact path ="/matching/viewQualificationInfo/:id" component={ViewQualificationInfo} />
        <Dashboard />
        </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
