import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom'

export default class SkillAdded extends Component {
    state = {
        skill: null,
        problemSkill: null,
        errorInfo: null,
        urlchecker: "" //for testing purposes remove when done
    };


async componentDidMount() {
    const { skill } = this.props.match.params;
    const token = await localStorage.getItem("token");
    var url;
    if (token!=null){
      url = `https://careers-server.herokuapp.com/addNewSkill?${skill}&authenticationToken=${token}`;
    }
    else{
      url = `https://careers-server.herokuapp.com/addNewSkill?${skill}`;
    }
    //Attempts to get results set from API JSON
    try{
    const res = await axios.get(url);
    //console.log({skill: res.data}); //for testing
    this.setState({skill: res.data});
    }
    //if there is an error in API records the last part of the error message to return to the user
    catch (error){
      //console.log(error.response.data) //for testing
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({problemSkill: skill})
      this.setState({errorInfo: seperateErrorMessages[3]})
    }
  }

  retryAddSkill(){
    return <Redirect to={`/matching/addNewSkill/${this.state.problemSkill}&${this.state.errorInfo}`} />
  }

    render() {
        return (
            <React.Fragment>
        {this.state.skill ? (
          <div>
          
            {this.state.skill.name}
            <br/><br/>
            {this.state.skill.category.skillType}
            <br/><br/>
            {this.state.skill.category.businessSector}
            <br/><br/>
          </div>
        ) : ( this.state.errorInfo ? (
        <h1>{this.state.errorInfo} {this.retryAddSkill()}</h1>) :(<h1>Loading Skill</h1>)
        )}
      </React.Fragment>      
        );
    }
}


