import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom'

export default class ContactAdded extends Component {
    state = {
        contact: null,
        problemContact: null,
        errorInfo: null,
        urlchecker: "" //for testing purposes remove when done
    };


async componentDidMount() {
    const { contact } = this.props.match.params;
    const url = `https://careers-server.herokuapp.com/addNewContact?${contact}`;
    //Attempts to get results set from API JSON
    try{
    const res = await axios.get(url);
    //console.log({contact: res.data}); //for testing
    this.setState({ contact: res.data});
    }
    //if there is an error in API records the last part of the error message to return to the user
    catch (error){
      //console.log(error.response.data) //for testing
      var seperateErrorMessages = error.response.data.split(":");
      this.setState({problemContact: contact})
      this.setState({errorInfo: seperateErrorMessages[3]})
    }
  }

  retryAddContact(){
    return <Redirect to={`/advertising/addNewContact/${this.state.problemContact}&${this.state.errorInfo}`} />
  }

    render() {
        return (
            <React.Fragment>
        {this.state.contact ? (
          <div>
          
            {this.state.contact.name.firstName}
            <br/><br/>
            {this.state.contact.name.secondName}
            <br/><br/>
            {this.state.contact.telephone}
            <br/><br/>
            {this.state.contact.email}
            <br/><br/>
            {this.state.contact.organisation.name}
          </div>
        ) : ( this.state.errorInfo ? (
        <h1>{this.state.errorInfo} {this.retryAddContact()}</h1>) :(<h1>Loading Contact</h1>)
        )}
      </React.Fragment>      
        );
    }
}


