import React, { Component } from "react";
import { Link } from "react-router-dom";

class AddNewQualification extends Component {
  state = {
    course: "",
    level: "",
    qualification: null
  };

  async componentDidMount() {
    const { qualification } = this.props.match.params;
    if (qualification) {
      var propertySplitter = qualification.split("&");
      this.setState({ course: propertySplitter[0].split("=")[1] });
      this.setState({ level: propertySplitter[1].split("=")[1] });
      await this.setState({ error: propertySplitter[2] });
      await this.setState({
        qualification: `course=${this.state.course}&level=${this.state.level}`
      });
     
    }
  }

  async updateCourse(e) {
    await this.setState({ course: e });
    this.updateQualification();
  }

  async updateLevel(e) {
    await this.setState({ level: e });
    this.updateQualification();
  }

  async updateQualification() {
    await this.setState({
      qualification: `course=${this.state.course}&level=${this.state.level}`
    });
    //console.log(this.state.qualification); //for testing
  }

  render() {
    const { state } = this;
    return (
      <div className ="row">
        <form onSubmit={this.onSubmit}>
          <div className="formDivider">
            <label>
              Qualification Course Title:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.course}
                onChange={e => this.updateCourse(e.target.value)}
              />
            </label>
          
          <br />
          <br />
          
            <label>
              Qualification Level:
              <input
                style={{ marginLeft: "20px" }}
                type="text"
                value={state.level}
                onChange={e => this.updateLevel(e.target.value)}
              />
            </label>
            </div>
          <br />
         
          <Link
            to={`/matching/qualificationAdded/${this.state.qualification}`}
          >
           <button>Add Qualification</button>
          </Link>
        </form>
        <br /> <br />
        <div style={{ color: "red", fontWeight: "bold" }}>
          {this.state.error}
        </div>
      </div>
    );
  }
}

export default AddNewQualification;
